import * as React from "react";
import * as d3 from "d3";
import { currentTaskSelectionInput } from "../../lib/helper";

interface ItxTraceProps {
  tsRange: currentTaskSelectionInput;
  brushItxData: {itxStart: number, itxEnd: number}[];
  chroniclesItxData: {ts: number, itxType: string}[];
  visWidth?: number;
  visHeight?: number;
  shapeSize?: number;
}

const defaultProps = {
  visWidth: 700,
  visHeight: 40,
  shapeSize: 10
};
export const ItxTrace: React.StatelessComponent<ItxTraceProps> = (p) => {
  const {tsRange, brushItxData, chroniclesItxData, visWidth, visHeight, shapeSize} = p;
  const timeScale = d3.scaleLinear()
                .rangeRound([1, visWidth])
                .domain([tsRange.startTime, tsRange.endTime]);
      const itxScale: {[index: string]: number} = {
        "brush": visHeight / 6,
        "delta": visHeight / 6 * 3,
        "navigate": visHeight / 6 * 5
      };
      const itxColorScale: {[index: string]: string} = {
        "brush": "steelblue",
        "delta": "orange",
        "navigate": "red"
      };
      const chroniclesShapes = chroniclesItxData.map(b => {
        if (b.ts < tsRange.startTime || b.ts > tsRange.endTime) {
          throw new Error(`Data is incorrect,
          interaction time out of bounds by ${Math.round((b.ts - tsRange.endTime) / 1000)}
          or ${Math.round((b.ts - tsRange.startTime) / 1000)}`);
        }
        return <circle
          cx={timeScale(b.ts)}
          cy={itxScale[b.itxType]}
          r={shapeSize / 2}
          fillOpacity={0.4}
          fill={itxColorScale[b.itxType]}
        ></circle>;
        }
      );
      const brushItxShapes = brushItxData.map((b, i) => {
        const rectWidth = Math.max(shapeSize / 2, timeScale(b.itxEnd) - timeScale(b.itxStart));
        // console.log("rectWidth", rectWidth, b.itxEnd, b.itxStart, timeScale(b.itxEnd) - timeScale(b.itxStart));
        return <rect
          x={timeScale(b.itxStart)}
          y={itxScale.brush + i % 2 * 6}
          width={rectWidth}
          height={shapeSize}
          fillOpacity={0.4}
          fill={itxColorScale.brush}
        ></rect>;
      });
      // draw one every 10 seconds
      let grid: JSX.Element[] = [];
      const skipTime = 5000;
      // const skipTime = (tsRange.endTime - tsRange.startTime) > 30000 ? 10000 : 5000;
      for (let i = tsRange.startTime; i < tsRange.endTime; i += skipTime) {
          grid.push(<line x1={timeScale(i)} y1="0"
                          x2={timeScale(i)} y2={visHeight}
                          stroke="lightgray" />);
      }
      grid.push(<line x1={timeScale(tsRange.endTime)} y1="0"
                      x2={timeScale(tsRange.endTime)} y2={visHeight}
                      stroke="gray" />);
  return <>
    <p>{Math.round((tsRange.endTime - tsRange.startTime) / 1000)} sec: {tsRange.wId} {tsRange.qId}</p>
    <svg width={visWidth + 50} height={visHeight}>
      {grid}
      {chroniclesShapes}
      {brushItxShapes}
    </svg>
  </>;
};

ItxTrace.defaultProps = defaultProps;


