import { Database } from "sql.js";

export type currentTaskSelectionInput = { startTime: number, endTime: number, wId: string, qId: string };

export type JSONObject = {[index: string]: string};

export type LogDataFormat = {
  type: string;
  name: string;
  values: JSONObject[];
};


export function simpleExec(db: Database, sql: string) {
  const scripts = sql.split("\n\n");
  scripts.forEach(s => {
    console.log(`Executing sql \n${s}`);
    db.run(s);
  });
}

export function hashCode(str: string) {
  let hash = 0;
  if (str.length === 0) {
      return hash;
  }
  for (let i = 0; i < str.length; i++) {
      let char = str.charCodeAt(i);
      hash = ((hash << 5) - hash) + char;
      hash = hash & hash; // Convert to 32bit integer
  }
  return hash;
}

/**
 * This is for the view taskCompletionTimes
 *   so annoying
 * @param correctTimes
 */
export function processTimes(correctTimes: string) {
  return correctTimes
    ? (correctTimes as string).split(",").map(n => {
      const v = n.split("-");
      return {
        time: parseInt(v[0], 10),
        wId: v[1]
      };
    })
    : [];
}

export const itxVisInstructions = `
Visualization reading instructions: blue rectangles represent interactions---long blue rectangle indicates interactions with latency (the first time), and short blue rectangle indicates a cached interaction that's instantenous.
The orange/red circles are the chronicles interaction (very few of them unfortunately, and was not used for the advantage of speed).
`;


