import * as React from "react";
import * as d3 from "d3";
import { analyzer } from "../setup";

const visWidth = 300;
const visHeight = 30;
const maxTasks = 10;
const maxTraining = 4;

enum Shape {
  TrainingPerf,
  TaskPerf,
}

interface WorkerSummaryState {
  shapeInfo: Map<Shape, string>;
}

export default class WorkerSummary extends React.Component<{}, WorkerSummaryState> {
  constructor(p: undefined) {
    super(p);
    this.clickShape = this.clickShape.bind(this);
    this.state = {
      shapeInfo: new Map(),
    };
  }

  clickShape(shape: Shape, shapeInfo: string) {
    this.setState(p => {
      p.shapeInfo.set(shape, shapeInfo);
      return p;
    });
  }
  test() {
    const v: {[index: string]: string} = {};
    v["isTraining"];
  }

  render() {
    const count = analyzer.getANumber("select count (distinct wId) from workerTasks");
    // a bit complex so got view
    const countCompleted = analyzer.getANumber("select count () from completedAll");
    const countCancelled = analyzer.getANumber("select count(distinct wId) from CancelSurvey");
    // failed should just work with the survey but logging was wrong... so countig instead.
    const countFailed = analyzer.getANumber(`select count() from successCountPerWorker where isTraining = 1 and countCorrect < 1`);
    const countChronicle = analyzer.getANumber(`
      select count(distinct wId) from (select wId from deltaItx union select wId from navigateItx)`);
    const allSuccessCountsByWorker = analyzer.GetStaticView("successCountPerWorker");
    const xTaskCount = d3.scaleLinear().rangeRound([1, visWidth]).domain([0, maxTasks]);
    const taskSuccessVis = <svg width={visWidth + 50} height={visHeight}>
        {allSuccessCountsByWorker.filter(v => !v["isTraining"]).map(v => (
          <circle
          cx={xTaskCount(v.countCorrect as number)}
          cy={visHeight * Math.random()}
          r={3}
          fillOpacity={0.5}
          fill={"steelblue"}
          onClick={() => this.clickShape(Shape.TaskPerf, `${v.isTraining ? "training" : "task"} ${v.countCorrect}`)}
          ></circle>)
        )}
        <text x={xTaskCount(maxTasks)} y={visHeight / 2}>{maxTasks}</text>
      </svg>;
      const xTrainingCount = d3.scaleLinear().rangeRound([1, visWidth]).domain([0, maxTraining]);
      const trainingSuccessVis = <svg width={visWidth + 50} height={visHeight}>
          {allSuccessCountsByWorker.filter(v => !v.isTraining).map(v => (
            <circle
            cx={xTrainingCount(v.countCorrect as number)}
            cy={visHeight * Math.random()}
            r={3}
            fillOpacity={0.5}
            fill={"steelblue"}
            onClick={() => this.clickShape(Shape.TrainingPerf, `${v.isTraining ? "training" : "task"} ${v.countCorrect}`)}
            ></circle>)
          )}
          <text x={xTaskCount(maxTasks)} y={visHeight / 2}>{maxTraining}</text>
        </svg>;
    return <>
      <table>
        <tr>
          <th>Total</th>
          <th>Complete</th>
          <th>Cancelled</th>
          <th>Failed</th>
          <th>No Chronicle</th>
        </tr>
        <tr>
          <td>{count}</td>
          <td>{countCompleted}</td>
          <td>{countCancelled}</td>
          <td>{countFailed}</td>
          <td>{count - countChronicle}</td>
        </tr>
      </table>
      <br/>
      <table style={{width: 500}}>
        <tr>
          <td style={{width: 100}}>Task Success</td>
          <td style={{width: visWidth + 50}}>{taskSuccessVis}</td>
          <td style={{width: 100}}>{this.state.shapeInfo.get(Shape.TaskPerf)}</td>
        </tr>
        <tr>
          <td style={{width: 100}}>Training Success</td>
          <td style={{width: visWidth + 50}}>{trainingSuccessVis}</td>
          <td style={{width: 100}}>{this.state.shapeInfo.get(Shape.TrainingPerf)}</td>
        </tr>
      </table>
    </>;
  }
}


