import * as React from "react";
import * as d3 from "d3";
import { analyzer } from "../setup";
import { gaussianRandom } from "../lib/stats";
import { processTimes, itxVisInstructions } from "../lib/helper";

export default class TaskSummary extends React.Component<{}, undefined> {
  constructor(p: undefined) {
    super(p);
  }
  render() {
    const overAllWidth = 800;
    const visWidth = 700;
    const visHeight = 30;
    const skipTime = 5000;
    const shapeSize = 10;
    // get the task info --- general distribution of how long something takes?
    // get a violin plot of the completion time by the task
    const rRaw = analyzer.GetStaticView("taskCompletionTimes");
    // this ugliness is because we do not have reudce for sql.js....
    const data = rRaw.map(v => ({
      qId: v.qId as string,
      qRank: v.qRank as number,
      question: v.question as string,
      correctTimes: processTimes(v.correctTimes as string),
      incorrectTimes: processTimes(v.incorrectTimes as string),
    }));
    const visDiv = data.sort((a, b) => (a.qRank > b.qRank) ? 1 : 0)
                       .map(r => {
      // some buffer range
      const range = [0, d3.max(r.correctTimes.concat(r.incorrectTimes), t => t.time) * 1.05];
      const x = d3.scaleLinear().rangeRound([1, visWidth]).domain(range);
      let grid: JSX.Element[] = [];
      for (let i = 0; i < range[1]; i += skipTime) {
        grid.push(<line x1={x(i)} y1="0"
                        x2={x(i)} y2={visHeight}
                        stroke="lightgray" />);
      }
      const genCircle = (t: {time: number, wId: string}, c: boolean) => <circle
        cx={x(t.time)}
        cy={gaussianRandom(0, visHeight)}
        r={shapeSize / 2}
        fillOpacity={0.5}
        fill={c ? "steelblue" : "red"}
        className="clickable"
        onClick={() => {
          console.log("inserting into taskSelectionInput", t.wId, r.qId);
          analyzer.NewInput("taskSelectionInput", {
              $wId: t.wId,
              $qId: r.qId,
            });
        }}
        ></circle>;
      const circles = r.correctTimes.map(t => genCircle(t, true))
                        .concat(r.incorrectTimes.map(t => genCircle(t, false)));
      return <>
        <tr style={{width: overAllWidth}}>
          <td colSpan={2}  dangerouslySetInnerHTML={{ __html: r.question }}>
          </td>
        </tr>
        <tr>
          <td style={{width: visWidth}}><svg width={visWidth} height={visHeight}>{grid}{circles}</svg></td>
          <td>{Math.round(range[1] / 1000)} seconds</td>
        </tr>
      </>;
    });
    return <>
      <p>
        Each point is a task completed by a turker, plotted in time. Red means the task was wrong, and blue means it's correct.
        You can click on any points to see a visualization of that task's trace
      </p>
      <p>
        {itxVisInstructions}
      </p>
      <table style={{width: overAllWidth}}>
        {visDiv}
      </table>
    </>;
  }
}


