export const UsabilitySurveyQuestionsShared = new Map<string, string>();

UsabilitySurveyQuestionsShared.set("tool", "Do you feel like the tool supported your performing the task?");
UsabilitySurveyQuestionsShared.set("chronicles" , "Did you find the ability to navigate between histories helpful?");
UsabilitySurveyQuestionsShared.set("delayFrustration" , "Were the delays frustrating?");
UsabilitySurveyQuestionsShared.set("delayDifficulty" , "Did the delays make thing difficult?");

export function getUsabilitySurveySequence (IsChronicles: boolean) {
  return [
    "tool",
    "delayFrustration",
    "delayDifficulty",
    ...(IsChronicles ? ["chronicles"] : [])
  ];
}


