import * as React from "react";
import * as d3 from "d3";
import { analyzer } from "../setup";
import { getUsabilitySurveySequence, UsabilitySurveyQuestionsShared,
  FlowSurveyQuestionsShared, FlowSurveySequenceShared } from "../../../shared/surveyContent";


interface UnitSurveySummaryProps {
  survey: "FlowSurvey" | "ToolSurvey";
}

const visHeight = 30;
const shapeSize = 5;
export const UnitSurveySummary: React.StatelessComponent<UnitSurveySummaryProps> = (p) => {
  const { commentsView, view, surveySequence, surveyQuestions} = p.survey === "FlowSurvey"
    ? {
      commentsView: "flowComments",
      view: "flowResults",
      surveySequence: FlowSurveySequenceShared,
      surveyQuestions: FlowSurveyQuestionsShared
    }
    : {
      commentsView: "toolComments",
      view: "toolResults",
      surveySequence: getUsabilitySurveySequence(true),
      surveyQuestions: UsabilitySurveyQuestionsShared
    };
  // answers, freeforms
  // : {qId: string, choices: number[]}[]
  const comments = (p.survey === "ToolSurvey") ? analyzer.GetStaticView(commentsView).map(v => v.answer as string) : [];
  const answers = analyzer.GetStaticView(view).map(r => ({
    qId: r.qId,
    answers: r.answers ? (r.answers as string).split(",").map(n => parseInt(n, 10)) : null,
  }));
  // note that the domain is pretty hardcoded
  const xScale = d3.scaleLinear().rangeRound([1, 300]).domain([1, 6]);

  const answersDiv = surveySequence.map((q, i) => {
    const question = surveyQuestions.get(q);
    const answerData = answers.filter(a => a.qId === q)[0].answers;
    return <tr>
      <td style={{width: 500}}>{i + 1}. {question}</td>
      <td><svg width={300} height={visHeight}>{answerData.map(a => <circle
        cx={xScale(a)}
        cy={visHeight * Math.random()}
        r={shapeSize}
        fillOpacity={0.5}
        fill={"steelblue"}
      ></circle>)}</svg></td>
    </tr>;
  });

  return <>
    <table style={{width: 800, borderBottom: "solid"}}>
      <tr>
        <th style={{width: 500}}>Question</th>
        <th style={{align: "left", width: 100}}>Not at all</th>
        <th style={{align: "left", width: 100}}>Partly</th>
        <th style={{align: "left", width: 100}}>Very much</th>
      </tr>
    </table>
    <table style={{width: 800}}>
      {answersDiv}
    </table>
    {comments.map(f => <p>{f}</p>)}
  </>;
};


