// https://stackoverflow.com/questions/25582882/javascript-math-random-normal-distribution-gaussian-bell-curve
// apparently an approxuimationm validated by the central limit theorem
export function gaussianRandom(start: number, end: number) {
  let rand = 0;
  for (let i = 0; i < 6; i += 1) {
    rand += Math.random();
  }
  rand = rand / 6;
  return Math.floor(start + rand * (end - start + 1));
}


