import * as React from "react";
import { analyzer } from "../setup";
import { JSONObject, currentTaskSelectionInput } from "../lib/helper";
import { ItxTrace } from "./snippet/ItxTrace";
// the goal here is to get a sense for each turker, what they did at a coarse granularity (brush, navigate, arrow)
// this takes a wId

interface InteractionVisState {
  wId: string;
  qId: string;
  tsRange: currentTaskSelectionInput;
  brushItxData: {itxStart: number, itxEnd: number}[];
  chroniclesItxData: {ts: number, itxType: string}[];
  hide: boolean;
}

export class InteractionVis extends React.Component<{}, InteractionVisState> {
  constructor(p: {}) {
    super(p);
    this.setBrushItxData = this.setBrushItxData.bind(this);
    this.setTsRange = this.setTsRange.bind(this);
    this.setChorniclesItxData = this.setChorniclesItxData.bind(this);
    this.hide = this.hide.bind(this);
    this.state = {
      wId: null,
      qId: null,
      tsRange: null,
      brushItxData: [],
      chroniclesItxData: [],
      hide: false,
    };

    analyzer.BindOutput("brushItxInTaskSelection", this.setBrushItxData);
    analyzer.BindOutput("chroniclesItxInTaskSelection", this.setChorniclesItxData);
    analyzer.BindOutput("currentTaskSelectionInput", this.setTsRange, {notNull: true});
  }

  setBrushItxData(data: JSONObject[]) {
    const brushItxData = data.map(d => ({
      itxStart: parseInt(d.itxStart, 10),
      itxEnd: parseInt(d.itxEnd, 10)
    }));
    console.log("brushItxData", brushItxData);
    this.setState({
      brushItxData,
      hide: false
    });
  }

  setChorniclesItxData(data: JSONObject[]) {
    const chroniclesItxData = data.map(d => {
      if (!(d.itxType === "delta" || d.itxType === "navigate")) {
        throw new Error(`itxType of chroniclesItxData should be delta or navigate`);
      }
      return {
        ts: parseInt(d.ts, 10),
        itxType: d.itxType
      };
    });
    console.log("chroniclesItxData", chroniclesItxData);
    this.setState({chroniclesItxData});
  }

  setTsRange(tsRangeRaw: currentTaskSelectionInput[]) {
    const tsRange = tsRangeRaw[0];
    console.log("tsRange", tsRange);
    this.setState({tsRange});
  }

  hide() {
    this.setState({hide: true});
  }

  render() {
    const { tsRange, chroniclesItxData, brushItxData, hide } = this.state;
    if (tsRange && brushItxData.length > 0 && !hide) {
      return <div className="floatingWidget">
      <ItxTrace
        tsRange={tsRange}
        brushItxData={brushItxData}
        chroniclesItxData={chroniclesItxData}
      />
      <button onClick={this.hide}>close</button>
      </div>;
    }
    return null;
  }
}


