import IDiel from "../../../shared/IDiel";
import { loadPage } from "../index";
import { dbFiles, sqlFilePaths } from "./config";
import { getPrepareQueries } from "./codegen";

const OutputTables = ["workerTasks"];

export class Analyzer extends IDiel {

  constructor() {
    super("analsysis");
    this.setup();
  }

  async setup() {
    await this.LoadDb(dbFiles.get("final"));
    await this.setupOutputStmts();
    this.setupInputStmts();
    loadPage();
  }

  async setupInputStmts() {
    const inputSQL = await (await fetch(sqlFilePaths.get("input"))).text();
    const preps = getPrepareQueries(inputSQL);
    preps.forEach(q => {
      console.log(`\nPrepare Stmt\n${q.query}\n`);
      this.input.set(q.name, this.db.prepare(q.query));
    });
  }
  async setupOutputStmts() {
    let names = OutputTables.slice();
    const analysisProgramSQL = await (await fetch(sqlFilePaths.get("program"))).text();
    const newAnalysisProgramSQL = await (await fetch(sqlFilePaths.get("newProgram"))).text();
    (analysisProgramSQL + newAnalysisProgramSQL).match(/create table \w+/ig).map(m => {
      const vName = m.slice(13);
      names.push(vName);
    });
    const analysisViewSQL = await (await fetch(sqlFilePaths.get("analysisViews"))).text();
    const outputProgramSQL = await (await fetch(sqlFilePaths.get("output"))).text();
    (analysisViewSQL + outputProgramSQL).match(/create view \w+/ig).map(m => {
      const vName = m.slice(12);
      names.push(vName);
    });
    names.map(o => {
      try {
        this.output.set(o, this.db.prepare(`select * from ${o}`));
      } catch (e) {
        console.log(`%c Failed to output ${o}`, "color: red");
        // throw new Error(e);
      }
    });
  }

  getANumber(sql: string) {
    try {
      const r = this.db.exec(sql);
      if (r && r[0] && r[0].values && r[0].values.length > 0) {
        return r[0].values[0][0] as number;
      } else {
        console.log(`%c SQL ${sql} did not return any results`, "color: #FF2400");
        return NaN;
      }
    } catch (e) {
      console.log(`%c Query did not execute\n${sql} because ${e}`, "color: #FF2400");
      return NaN;
    }
  }
}


