import * as React from "react";
import { analyzer } from "../setup";
import { ItxTrace } from "./snippet/ItxTrace";
import { currentTaskSelectionInput, itxVisInstructions } from "../lib/helper";

export const AllItxTraces: React.StatelessComponent<{}> = () => {
  // get the values
  const tsRanges = analyzer.GetStaticView("allCorrectNonMemTasks") as currentTaskSelectionInput[];
  const brushItxData = analyzer.GetStaticView("correctNonMemBrushTraces") as  {wId: string, qId: string, itxStart: number, itxEnd: number}[];
  const chroniclesItxData = analyzer.GetStaticView("correctNonMemChroniclesTraces") as {wId: string, qId: string, ts: number, itxType: string}[];
  const divs = tsRanges.map(d =>
    <ItxTrace
      tsRange={d}
      brushItxData={brushItxData.filter(b => (b.wId === d.wId) && (b.qId === d.qId))}
      chroniclesItxData={chroniclesItxData.filter(b => (b.wId === d.wId) && (b.qId === d.qId))}
    />);
  return <>
    <p>This is a list of the traces of all (non memory) tasks that were <b>correct</b>.</p>
    <p>
      {itxVisInstructions}
    </p>
    {divs}
  </>;
};


