
import * as React from "react";
import WorkerSummary from "./WorkerSummary";
import TaskSummary from "./TaskSummary";
import { UnitSurveySummary } from "./UnitSurveySummary";
import { InteractionVis } from "./InteractionVis";
import { AllItxTraces } from "./AllItxTraces";

interface AnalysisContainerState {
  content: Summaries;
}

enum Summaries {
  Worker,
  Task,
  Trace,
  FlowSurvey,
  ToolSurvey
}

export default class AnalysisContainer extends React.Component<{}, AnalysisContainerState> {
  constructor(p: undefined) {
    super(p);
    this.setView = this.setView.bind(this);
    this.state = {
      content: Summaries.Task
    };
  }

  setView(content: Summaries) {
    return () => {
      this.setState({content});
    };
  }

  render() {
    const { content } = this.state;
    let contentDiv: JSX.Element;
    if (content === Summaries.Trace) {
      contentDiv = <AllItxTraces />;
    }
    if (content === Summaries.Task) {
      contentDiv = <><TaskSummary /><InteractionVis /></>;
    }
    if (content === Summaries.Worker) {
      contentDiv = <WorkerSummary />;
    }
    if (content === Summaries.FlowSurvey) {
      contentDiv = <UnitSurveySummary survey="FlowSurvey" />;
    }
    if (content === Summaries.ToolSurvey) {
      contentDiv = <UnitSurveySummary survey="ToolSurvey" />;
    }
    return <div className="container">
    <div className="summary">
      <div className="controls">
        <button className={`general-btn ${(content === Summaries.Task) ? "clicked-btn" : ""}`} onClick={this.setView(Summaries.Task)}>show task</button>
        <button className={`general-btn ${(content === Summaries.Trace) ? "clicked-btn" : ""}`} onClick={this.setView(Summaries.Trace)}>show all traces</button>
        <button className={`general-btn ${(content === Summaries.Worker) ? "clicked-btn" : ""}`} onClick={this.setView(Summaries.Worker)}>show worker</button>
        <button className={`general-btn ${(content === Summaries.FlowSurvey) ? "clicked-btn" : ""}`} onClick={this.setView(Summaries.FlowSurvey)}>show flow survey</button>
        <button className={`general-btn ${(content === Summaries.ToolSurvey) ? "clicked-btn" : ""}`} onClick={this.setView(Summaries.ToolSurvey)}>show tool survey</button>
      </div>
      <div className="portal">
        {contentDiv}
      </div>
    </div>
  </div>;
  }
}


