import { Duplicates } from "./config";
import { LoggedTablesForAnalysis } from "../../../shared/logHelper";
// this takes a .sql file and generates the corresponding into a ts file

/* e.g., from

CREATE TABLE eventLog (
  ts INTEGER NOT NULL,
  eventName TEXT NOT NULL,
  parameter TEXT
);

to

eventLog: db.prepare(`
    insert into eventLog (ts, eventName, parameter)
    values ($ts, $eventName, $parameter);
  `),
*/

function getColumns(tableSQL: string) {
  const queries = tableSQL.split(");");
  const r = queries.filter(q => q && q.length > 0 && (q !== "\n")).map(q => {
    const tableName = q.match(/create table \w+/ig)[0].slice(13);
    const colNames = q.match(/(\w+) (integer|text)/ig).map(v => v.split(" ")[0]);
    return {tableName, colNames};
  });
  return r;
}
/**
 * This generates the prepared statement that skipps duplicates
 *   - could not use https://stackoverflow.com/questions/19337029/insert-if-not-exists-statement-in-sqlite
 *     since it conflates differnet kinds of failing constraints, e.g., not null
 *   - also must not include taskId when checking for duplicates since it's generated on the gpaas side
 *     and for the same values would have different taskIds.
 * @param tableSQL sql strings
 */
export function getPrepareQueries(tableSQL: string) {
  const i = getColumns(tableSQL);
  return i.map(s => ({
    name: s.tableName,
    query: `insert into ${s.tableName} (${s.colNames.join(", ")})
            values (${s.colNames.map(v => `$${v}`).join(", ")});`
  }));
}

export function getDedupeQueries(tableSQL: string) {
  const i = getColumns(tableSQL);
  return i.filter(s => Duplicates.indexOf(s.tableName) > -1).map(s => {
    const cols = s.colNames.filter(v => v !== "taskId").join(", ");
    return ({
      name: s.tableName,
      query: `create table temp as
        select distinct ${cols}
        from ${s.tableName};
      delete from ${s.tableName};
      insert into ${s.tableName} (${s.colNames.join(", ")})
        select ${s.colNames.join(", ")} from temp;
      drop table temp;`
    });
  });
}

/**
 * This method is very brittle and we should fix
 * @param tableSQL input SQL
 * @param print whether to show the results
 */
export function simpleParse(tableSQL: string, print = false) {
  let scripts = tableSQL.split("\n\n");
  let newScripts: string[] = [];
  for (let s of scripts) {
    s = s.replace(/^ *--.*$\n/img, "");
    s = s.replace(/^drop.*$\n/img, "");
    // also get rid of primary key requirements
    s = s.replace(/PRIMARY KEY/i, "not null");
    // need to delete unique, also if it's the last one, need to remove the comma
    s = s.replace(/,\n^.*UNIQUE.*$\n\);/img, "\n);");
    // then remove the none last items
    s = s.replace(/^.*UNIQUE.*$\n/img, "");
    // then before the ");", need to insert the wId and taskID, at the top after the first ( with new line
    const tmp = s.split(`(\n`);
    // assert that there are only 2
    if (tmp.length !== 2) {
      if (print) console.log(`[Not ran]\n${s}\n`, "color: red");
      continue;
    }
    if (tmp[0].includes("insert into")
          || tmp[0].includes("ChartInitData")
          || tmp[0].includes("ChartData")
        ) {
      if (print) console.log(`[Not ran]\n${s}\n`, "color: red");
      continue;
    }
    const name = tmp[0].match(/create table \w+/ig)[0].slice(13);
    if (LoggedTablesForAnalysis.indexOf(name) === -1) {
      if (print) console.log(`[Not ran, not logged]\n${s}\n`, "color: red");
      continue;
    }
    // and then stitch together after
    s = tmp[0]
        + `(\n  wId integer not null,${Duplicates.indexOf(name) > -1 ? "" : "\n  taskId integer not null,"}\n`
        + tmp[1];
    if (print) console.log(`[executing script]\n${s}\n`);
    newScripts.push(s);
  }
  return newScripts;
}


